.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.label {
  text-transform: uppercase;
  font-size: 20px;
  color: var(--green);
  transition: all 200ms;
  width: 100%;
}

.label__focus {
  color: var(--grey);
}

.label__invalid {
  color: var(--red);
}

.input {
  font-size: 32px;
  min-height: 40px;
  text-align: center;
  padding-top: 8px;
  margin-top: 5px;
  background-color: var(--green);
  color: var(--dark-green);
  caret-color: transparent;
  transition: all 200ms;
}

.input__filled,
.input:focus {
  background-color: var(--grey);
}
