.training {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.task-description {
  display: flex;
  width: 100%;
  gap: 10px;
  justify-content: space-between;
}

.process {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.current-task-label {
  text-align: center;
  color: var(--yellow);
  font-size: 20px;
  margin-top: 10px;
}

.form {
  display: flex;
  justify-content: center;
}

.input {
  max-width: 150px;
  text-align: center;
  padding-top: 10px;
  font-size: 64px;
  color: var(--yellow);
  background-color: var(--dark-green);
  caret-color: transparent;
  border: 2px solid var(--green);
  border-radius: 10px;
  margin-top: 20px;
}

.input:focus {
  border-color: var(--yellow);
}

.answer-check-label {
  text-align: center;
  margin-top: 10px;
  text-transform: uppercase;
  font-size: 16px;
  color: var(--yellow);
}

.answer-check-label__wrong {
  color: var(--red);
}
