.sequence-order-wrapper {
  margin: 0 auto;
}

.sequence-order {
  display: flex;
  flex-wrap: wrap;
  width: 343px;
  justify-content: flex-start;
  gap: 8.5px;
  margin-top: 10px;
}

.sequence-number {
  min-width: 50px;
  min-height: 24px;
  font-size: 20px;
  padding-top: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--yellow);
  color: var(--dark-green);
}
