.radio-set {
  width: 100%;
}

.radio-set-heading {
  font-size: 20px;
  color: var(--green);
  text-transform: uppercase;
  text-align: end;
}

.radio-group {
  display: flex;
  margin-top: 5px;
  justify-content: stretch;
  gap: 15px;
}

.radio-input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.radio-wrapper {
  flex: 1 1 0;
}

.radio-input:checked + .radio-label {
  background-color: var(--grey);
}

.radio-label {
  width: 100%;
  height: 75px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 3px;
  background-color: var(--green);
  color: var(--dark-green);
  font-size: 32px;
  transition: background-color 200ms;
}
